body {
  background-color: #f6f6f6;
}

.sl-title {
  letter-spacing: -.04em;
  color: #111;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.empty-cart {
  width: 265px;
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
}

.sl-part {
  color: #4f2ee8;
}

.sl-list > li {
  margin-bottom: 20px;
}

.trash-btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background: #4f2ee8;
  border: none;
  border-radius: 50%;
  padding: 12px;
  position: relative;
  top: 14px;
  right: 14px;
}

.trash-btn > svg {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sl-card {
  height: 170px;
  width: 100%;
  background-color: #fff;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  display: flex;
}

.sl-book-img {
  border-radius: 8px;
  margin: 14px;
}

.sl-book-cat {
  letter-spacing: .03em;
  color: #b4afaf;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.sl-book-title {
  max-width: 170px;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #111;
  margin-top: 14px;
  margin-bottom: 4px;
  overflow: hidden;
}

.sl-book-descr {
  height: 68px;
  letter-spacing: -.04em;
  color: #111;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  overflow: scroll;
}

.sl-book-author {
  letter-spacing: -.04em;
  color: #b4afaf;
  max-width: 90px;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: auto;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16;
  overflow: hidden;
}

.sl-aut-shops, .sl-eshops {
  justify-content: center;
  align-items: center;
  display: flex;
}

.sl-eshops > li:not(:last-child) {
  margin-right: 8px;
}

.sl-message {
  text-align: center;
  letter-spacing: -.02em;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 14px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
}

@media screen and (min-width: 768px) {
  .sl-card {
    height: 213px;
  }

  .sl-book-img {
    width: 116px;
    margin: 24px;
  }

  .sl-book-title {
    width: auto;
    margin-top: 24px;
    overflow: visible;
  }

  .sl-book-cat {
    margin-bottom: 14px;
  }

  .sl-book-descr {
    margin-bottom: 20px;
    padding: 0;
  }

  .sl-book-author {
    overflow: visible;
  }

  .trash-btn {
    width: 34px;
    height: 34px;
    top: 24px;
    right: 24px;
  }

  .trash-btn > svg {
    width: 18px;
    height: 18px;
  }

  .sl-book-info {
    width: inherit;
  }

  .empty-cart {
    margin-top: 140px;
  }

  .sl-message {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.33;
  }

  .empty-cart > img {
    width: 322px;
  }

  .sl-amazon {
    width: 48px;
    height: 15px;
  }

  .sl-ibooks {
    width: 28px;
    height: 27px;
  }

  .sl-bookshop {
    width: 32px;
    height: 30px;
  }

  .sl-eshops > li:not(:last-child) {
    margin-right: 16px;
  }
}

/*# sourceMappingURL=shopping-cart.f60f32ca.css.map */
